import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
    {
        path: '',
        children: [
           
            
            {
                path: 'mshp-membership',
                loadChildren: () => import('./mshp-membership/mshp-membership.module').then((module) => module.MshpMembershipModule)
            }
            ,
            {
              path: 'mshp-membershipType',
              loadChildren: () => import('./mshp-membershipType/mshp-membershipType.module').then((module) => module.MshpMembershipTypeModule)
          },
            {
                path: 'mshp-membership-select',
                loadChildren: () => import('./mshp-membership-select/mshp-membership-select.module').then((module) => module.MshpMembershipSelectModule)
            }
            ,
            {
                path: 'user-profile',
                loadChildren: () => import('../membership/user-profile/user-profile.module').then((module) => module.UserProfileModule)
            }
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class MembershipRoutingModule {}
